<template>
	<el-form :model="dataForm" :rules="rules">
    <el-form-item label="" prop="name">
      <el-input type="text" v-model="dataForm.name" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" prop="email">
      <el-input type="text" v-model="dataForm.email" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" prop="password">
      <el-input type="password" v-model="dataForm.password" placeholder=""></el-input>
    </el-form-item>
    <el-button @click="register"></el-button>
    <div class="remind"></div>
  </el-form>
</template>

<script>
  export default {
    name: "Register",
    data() {
      return {
        dataForm: {name: '', password: '', email: ''},
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          email: [{required: true, message: '', trigger: 'blur'}],
        },
      }
    },
		methods: {
      register () {
        var params = new URLSearchParams();
        params.append('name', this.dataForm.name);
        params.append('email', this.dataForm.email);
        params.append('password',this.dataForm.password);
        this.axios.post('/user/register', params)
          .then((res)=>{
            var remindTitle = res.data.code == 0 ? '' : '';
            var remindContent = res.data.code == 0 ? '' : '/(o)/~~';
            if (res.data.code==0){
              this.$router.push("/");
            }
            this.$alert(remindContent, remindTitle, {
              confirmButtonText: '',
            });
          })
          .catch((res)=>{
            console.log(res.data.message);
          });
      }
    }
  }
</script>



