<template>
  <el-container>
    <el-header>
      <v-head></v-head>
    </el-header>
    <el-main>
      <div style="height: 360px">
        <el-image class="image" :src="url" :fit="fit"></el-image>
        <el-tabs v-model="activeName" style="display: inline-block; margin-left: 15%; height: 300px">
          <el-tab-pane label="" name="first">
            <v-login></v-login>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <v-register></v-register>
          </el-tab-pane>
        </el-tabs>
      </div>
      <el-card style="width: 800px; margin: auto">
        <div slot="header" class="cardTitle"></div>
        <div class="text item">
        </div>
      </el-card>
    </el-main>
    <v-footer></v-footer>
  </el-container>
</template>

<script>
  import vHead from './common/Header.vue';
  import vFooter from './common/Footer.vue';
  import vNotice from './common/Notice.vue';
  import vLogin from './Login.vue';
  import vRegister from './Register.vue';
  export default {
    name: "Home",
    data() {
      return {
        fit: 'fill',
        activeName: 'first',
        url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
        activeName2: 'first',
        type: "",
        title: "",
        author: "uc",
        keyWordTags: ['machine learning', 'c++'],
        main_text: "       ",
      }
    },
    components: {
      vHead,
      vFooter,
      vNotice,
      vLogin,
      vRegister
    },
    methods: {
    }

  }
</script>

<style>

  .el-main {
    background-color: #F4F4F5;
    color: #333;
  }

  .remind {
    display: inline-block;
    font-size: 8px;
    color: red;
    text-decoration: underline;
  }

  .image {
    width: 200px;
    height: 200px;
    display: inline-block;
    margin-left: 20%;
    vertical-align: top;
    padding-top: 32px;
  },

  .card {
    width: 800px;
    margin: auto;
  }

  .cardTitle {
    font-size: 20px;
    text-align: center;
  }
</style>



